'use strict';
angular.module('oneImobiliaria').service('ClientService', [
  '$http', '$state', 'RESOURCES', function($http, $state, RESOURCES) {
    var apiUrl;
    apiUrl = RESOURCES.API_URL + '/clients';
    return {
      create: function(client) {
        return $http.post(apiUrl, client);
      },
      update: function(client) {
        return $http.put(apiUrl + '/' + client._id, client);
      },
      saveOrUpdate: function(client) {
        if (client._id) {
          return this.update(client);
        } else {
          return this.create(client);
        }
      },
      getAll: function() {
        return $http.get(apiUrl);
      },
      get: function(id) {
        return $http.get(apiUrl + '/' + id);
      },
      "delete": function(id) {
        return $http["delete"](apiUrl + '/' + id);
      }
    };
  }
]);

