'use strict';
angular.module('oneImobiliaria').controller('UsersCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'UserGroupService', 'UserService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, UserGroupService, UserService) {
    $scope.user = {};
    $scope.users = [];
    $scope.file = {};
    $scope.fileName = 'Selecione um arquivo';
    $scope.groups = UserGroupService.getAll();
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      UserService.get($stateParams.id).then(function(response) {
        $scope.user = response.data;
        if ($scope.user.photo == null) {
          $scope.user.photo = false;
        }
        $scope.edit = false;
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar usuários. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      UserService.getAll().then(function(response) {
        $scope.users = $filter('orderBy')(response.data, '-created');
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar usuários. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.selectPhoto = function() {
      if ($scope.file.name == null) {
        $scope.file = {};
        $scope.fileName = 'Selecione um arquivo';
        return false;
      }
      return $scope.fileName = $scope.file.name;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.user.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return UserService.savePhoto($scope.file).then(function(response) {
        if (response.data.file != null) {
          $scope.user.photo = response.data.file;
        }
        return UserService.saveOrUpdate($scope.user);
      }).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.users');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar usuário. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var user;
      user = $scope.users[index];
      if (user._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return UserService["delete"](user._id).then(function(response) {
        $logger.success('Usuário excluído com sucesso!');
        $scope.users.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir usuário. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);
